    padding-bottom: 200px;
    background-color: rgb(230, 230, 230);
}

.events_heading{
    font-size: 40px;
    font-weight: bolder;
    color: rgb(0, 0, 43);
    font-weight: bold;
    font-family: 'Rajdhani', sans-serif;
    font-family: "Poppins", Arial, sans-serif;
    text-align: center;
    padding-bottom: 20px;
}

.root {
    min-height: 300px;
    margin-top: 20px;
    margin: 20px;
}

.event_name{
    font-family: 'Rajdhani', sans-serif;
    font-family: "Poppins", Arial, sans-serif;
    font-size: 30px;
    font-weight: bold;
    padding-bottom: 20px;
    padding-top: 10px;
    text-align: center;
}

.event_desc {
    font-family: 'Rajdhani', sans-serif;
    font-family: "Poppins", Arial, sans-serif;
    font-size: 17px;
    text-align: center;
}

.event_pic{
    height: 300px;
    width: 100%;
}

.chip{
    background-color: blue !important;
    color: white !important;
    margin-left: 20px;
}

.footer{
    padding-top: 50px;
    padding-bottom: 70px;
    background-color: black;
    color: white;
}

.footer_heading{
    padding-top: 50px;
    font-size: 30px;
    font-weight: lighter;
    font-family: 'Rajdhani', sans-serif;
}

.footer_logo {
    margin-right: 20px;
}

.footer_text{
    padding-top: 40px;
    font-size: 15px;
    font-weight: lighter;
    font-family: "Open Sans", Arial, sans-serif;
}

.footer_text2{
    padding-top: 25px;
    font-size: 15px;
    font-weight: lighter;
    font-family: 'Rajdhani', sans-serif;
    
}

.footer_link_space{
    margin-top: 10px;
    padding-bottom: 10px;
    border-bottom: 1px solid rgb(182, 181, 181);
}

.footer_link{
    font-size: 17px;
    color: lightgray;
    text-decoration: none;
    font-family: 'Rajdhani', sans-serif;
}

.footer_link:hover{
    text-decoration: none;
    color: white;
    font-weight: normal;
}

.footer_first_icon{
    color: white;
}

.footer_icon{
    color: white;
    margin-left: 30px;
}

.footer_footer {
    background-color: rgb(97, 96, 96);
    padding-top: 40px;
}

.sig_space{
    margin-top:100px;
    margin-bottom: 100px;
}

.sig_description{
    margin-bottom:50px;
    margin-left:25px;
    font-size: 20px;
    font-family: "Poppins", Arial, sans-serif;
    line-height: 1.7;
    font-weight: 400;
    color: rgb(78, 78, 78);
}

.yantra_events_div {
    height: auto;
    overflow: scroll;
}

.carousel{
    background-color: rgba(0, 0, 32, 1);
    width: 100%;
    color: white;
    height: 350px;
}

.carousel2{
    background-color: rgba(0, 0, 32, 1);
    width: 100%;
    color: white;
}

/* .carousel_indicators{
    padding-top: 100px;
} */

.event_pic{
    padding: 20px;
}
.proposal_card{
    text-align: center;
}

.proposal_name {
    margin-top: 50px;
    padding: 10px;
    font-size: 22px;
    font-family: 'Rajdhani', sans-serif;
    font-family: "Poppins", Arial, sans-serif;
    color: black;
    text-align: center;
}

.proposal_button {
    text-align: center;
    color: black;
    background-color: white;
    border: 1px solid black;
}

.proposal_img_box {
    text-align: center;
    padding: 10px;
}

.proposal_project_card_img {
    border: 1px solid black;
    border-radius: 10px;
}

.flipbook-heading{
    border-top: 1px solid black;
    text-align: center;
    padding: 30px;
}
